#diets{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}
.dietTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}
.dietDesc{
    font-weight: 300;
    font-size: 0.9rem;
    max-width: 51rem;
    padding: 0 2rem;
}
.skillBars{
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;
}
.dietBar{
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2 rem;
    border-radius: 0.5rem;
    background: rgb(50,50,50);
}
.dietBarImg{
    object-fit: cover;
    height: 22rem;
    width: 30rem;
    margin-right: 2rem;
}

.list{
    margin: 50px;
    font-size: 1rem;
    font-family: 200;
    text-align: left;
}
@media screen and (max-width: 720px) {
    .list{
        font-size: 2vw;
    }
}

@media screen and (max-width: 480px) {
    .list{
        font-size: 3vw;
    }
    .list>h2{
        font-size: 5vw;
    }
}