#bmi{
    margin: 0 auto;
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;
}
h2{
    font-size: 3rem;
}
input {
    width: 100%;
    font-size: 1.2rem;
    padding: 15px 4px;
    margin: 8px 0;
    border-radius: 8px;
    color: white;
  }
  .btn {
    display: block;
    width: 100%;
    font-size: 1.2rem;
    margin: 8px 0;
    padding: 15px 0;
    background-color: #0077EE;
    color: #fff;
    border: 1px solid #333;
    border-radius: 8px;
    cursor: pointer;
  }
  .btn-outline {
    background-color: #fff;
    color: #A6BCDA;
  }
  .center {
    text-align: center;
    margin: 24px 0;
  }
   
  p {
    margin: 10px 0;
  }